@import url("https://fonts.googleapis.com/css?family=Raleway:400,500");

@media only screen  and (max-width: 600px) {
  .buttons{
    flex-direction: column-reverse;
    justify-content: center;
  }
  #new-quote{
    margin-bottom: 10px;
  }
}
.App {
  align-items: center;
  #quote-box {
    background-color: white;
    border-radius: 10px;
    color: black;
    padding: 20px;
    #author {
      text-align: end;
    }
    #tweet-quote {
      float: left;
      padding-left: 5%;
    }
    #new-quote {
      float: right;
      color: white;
      background-color: black;
      border-radius: 5px;
      padding: 10px;
      border: none;
      cursor: pointer;
    }
  }
  .App_header {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px+2vmin);
    color: white;
    
  }
}
